:root {
    --main-font: "Inter", sans-serif;
    --regular-gap: 30px;

    @media screen and (max-width:991px) {
        gap: 15px;
    }
}

body {
    font-family: var(--main-font);
    background: #221f31;
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;

    &__img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.icon {
    width: 100%;
    height: 100%;
}


.page-wrapper {
    height: 100vh;
    min-height: 550px;
    display: flex;
    flex-direction: column;

    &__content {
        height: calc(100vh - 100px);
        min-height: 450px;
    }
}

.content {
    display: flex;
    align-items: stretch;

    @media screen and (max-width:991px) {
        flex-direction: column;
    }

    &__wrapper {
        padding: 30px;
        flex-grow: 1;

        @media screen and (max-width:640px) {
            padding: 30px 15px;
        }
    }

    &__aside {
        background-color: #1f1c2d;

        max-width: 300px;
        width: 100%;
        flex-shrink: 0;

        @media screen and (max-width:991px) {
            display: none;
        }
    }
}


.page {
    &-grid {
        display: flex;
        align-items: stretch;
        gap: var(--regular-gap);

        height: 100%;

        @media screen and (max-width:991px) {
            flex-direction: column;
        }
    }

    &-content {
        display: flex;
        flex-direction: column;
        gap: 30px;
        flex-grow: 1;
    }
}

.block {
    max-height: 100%;
    overflow: hidden;


    background-color: #1E1C2D;
    border-radius: 20px;

    @include custom-scrollbar;

    padding: 25px 20px 0px 30px;

    position: relative;

    @media screen and (max-width:991px) {
        padding: 15px 15px 5px 15px;
    }

    &::before{
        content: '';
        width: 100%;
        height: 100%;
        display: block;
        bottom: 0;
        left: 0;
        position: absolute;
        background: linear-gradient(0deg, rgba(#221f31,0.8), rgba(#221f31,0));

        transition: .3s visibility,.5s opacity;
        z-index: 3;
    }

    &:hover::before{
        visibility: hidden;
        opacity: 0;
    }

    &--scrolled &__content{
        &::before{
            visibility: hidden;
            opacity: 0;
        }
    }


    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 15px;

        @media screen and (max-width:640px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        max-height: 100%;
        gap: var(--regular-gap);

        @include custom-scrollbar;
        overflow: auto;

        padding: 10px 10px 35px 0;

        @media screen and (max-width:991px) {
            padding: 15px 15px 10px 0;
        }
    }

    &__content{
        position: relative;

    }

    &__title {
        font-size: 20px;
        font-weight: 700;
        line-height: 1.2;
        color: #ffffff;
    }
}

.text {
    font-family: var(--main-font);
    color: #fff;

    &--base {
        font-size: 14px;
        font-weight: 600;
        line-height: 1.2;
    }
}

.select {
    position: relative;
    z-index: 10;

    &--period{
        max-width: 260px;
        width: 100%;
    }
    &__tag {
        display: none;
    }

    &__wrapper {
        position: relative;
        max-width: 260px;
        width: 100%;
    }

    &__label,
    &__item {
        font-family: var(--main-font);
        font-size: 14px;
        font-weight: 700;
        color: #fff;
        padding: 15px 20px;
        background: #2a2540;
        border-radius: 10px;

        font-size: 13px;
        line-height: 1.4;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;

        transition: .3s border-radius;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 100%;
        &:hover {
            cursor: pointer;
        }
    }

    &__item {
        border-radius: 0;
        width: 100%;
        transition: .3s background-color;
        &:not(:last-child) {
            border-bottom: 1px solid rgba(#fff,.05);
        }
    }

    &__label {

        display: flex;
        align-items: center;
        gap: 20px;
        border-bottom: 1px solid transparent;
        transition: .3s border-color,.3s border-radius;
        &::after {
            content: '';
            width: 0;
            height: 0;
            display: block;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;

            border-top: 5px solid #59517c;

            transition: .3s transform;
        }

        &--active {
            border-radius: 10px 10px 0 0;
            border-color: rgba(#fff,.05);
            &::after{
                transform: rotate(180deg);
            }
        }
    }

    &__content {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;

        border-radius: 0 0 10px 10px;
        overflow: hidden;

        display: flex;
        flex-direction: column;

        border: 1px solid var(--white-color);
        border-top: none;

        transition: .3s opacity, .1s visibility;

        &--hidden {
            opacity: 0;
            visibility: hidden;
        }
    }
}